.CitySearch {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.city {
  width: 65%;
  display: inline;
}

.suggestionList {
  transition-duration: .5s;
}

.suggestionList:hover {
  opacity: .5;
  cursor: pointer;
  transform: scale(1.1);
}

.suggestion {
  transition-duration: 1s;
}